"use client";

import { useState } from 'react';

const faqs = [
  { q: '서비스 이용 방법은 어떻게 되나요?', a: '회원가입 후 원하는 서비스를 선택하여 신청하시면 됩니다. 자세한 절차는 안내 문서를 참고하세요.' },
  { q: '비밀번호를 잊어버렸어요.', a: '로그인 페이지의 "비밀번호 찾기" 기능을 이용해주세요.' },
  { q: '결제는 어떤 방식으로 이루어지나요?', a: '신용카드, 계좌이체 등 다양한 결제 수단을 지원합니다.' },
  { q: '기술 지원은 어떻게 받을 수 있나요?', a: '고객지원 페이지를 통해 문의하시거나, 원격 지원을 요청하실 수 있습니다.'}
];

export default function FaqSection() {
  const [openIndex, setOpenIndex] = useState<number | null>(null);

  const toggleFaq = (index: number) => {
    setOpenIndex(openIndex === index ? null : index);
  };

  return (
    <section className="bg-[#04AAAB] py-20 px-4">
      <div className="max-w-3xl mx-auto">
        <h2 className="text-white text-3xl font-semibold text-center mb-12">자주 묻는 질문 (FAQ)</h2>
        <div className="space-y-4">
          {faqs.map((item, idx) => (
            <div key={idx} className="bg-white rounded-lg shadow-md overflow-hidden">
              <button
                onClick={() => toggleFaq(idx)}
                className="w-full flex items-center justify-between text-gray-800 py-4 px-6 focus:outline-none"
              >
                <span className="text-sm md:text-base font-medium text-left">Q. {item.q}</span>
                <span className={`transform transition-transform duration-300 ${openIndex === idx ? 'rotate-180' : ''}`}>
                  <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" /></svg>
                </span>
              </button>
              <div
                className={`transition-all duration-300 ease-in-out ${openIndex === idx ? 'max-h-96' : 'max-h-0'}`}
                style={{ transitionProperty: 'max-height, padding' }}
              >
                <div className="px-6 pb-4 text-sm text-gray-600">
                  <p>A. {item.a}</p>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
} 